.site-container {
  max-width: 800px;
  margin: 0 auto; }
  .site-container__inner {
    padding: 20px; }

.site-header__title {
  margin: 1.5rem 0;
  font-size: 48px;
  font-family: "Fira Sans", sans-serif;
  font-weight: 700; }

.site-intro {
  margin-bottom: 40px; }
  .site-intro__content > p {
    margin: 0;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    line-height: 28px; }
  .site-intro__content > p + p {
    margin-top: 1rem; }

.site-footer__copyright {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  line-height: 28px; }

.articles {
  padding: 0 60px; }

.article__header {
  padding: 5px 0 5px 20px;
  border-width: 0 0 0 4px;
  border-style: solid;
  border-color: #ccc; }

.article__title {
  margin: 0;
  font-size: 36px;
  font-family: "Fira Sans", sans-serif;
  font-weight: 700; }

.article__date {
  font-size: 16px;
  font-family: "Montserrat", sans-serif; }

.article__content {
  padding: 0 24px 0 20px; }
  .article__content > h3 {
    margin: 2rem 0 0.75rem 0;
    font-size: 28px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 700; }
  .article__content > h4 {
    margin: 2rem 0 0.5rem 0;
    font-size: 20px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 700; }
  .article__content > p {
    margin: 0;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    line-height: 28px; }
  .article__content > p + p {
    margin-top: 1.5rem; }
  .article__content > p > img {
    display: block;
    max-width: 100%;
    height: auto; }

.article + .article {
  margin-top: 40px; }

.pagination {
  margin: 80px 0 40px;
  padding-top: 20px;
  border-top: 4px solid #f1f1f1; }
  .pagination__count {
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    line-height: 28px; }
